.pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.pagination button {
  margin: 0 5px;
  padding: 5px 10px;
  border: 1px solid #ddd;
  background-color: #f7f7f7;
  cursor: pointer;
}

.pagination button.active {
  font-weight: bold;
  background-color: #eaeaea;
}
